@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

// Form group elements
.input-wrap {
  margin-bottom: $form-bottom-space; }

$form-group-cols: 2;
$form-group-gap: 4rem;
$form-group-gap-half: round-float($form-group-gap / 2, 1);
@debug $form-group-gap;
.form-group {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  margin: 0 -#{$form-group-gap-half} $form-bottom-space;

  &:last-child {
    margin-bottom: 0; }

  @include media('<mob') {
    display: block; } }

.form-group__item {
  position: relative;
  $width: calc(100% / #{$form-group-cols} - #{$form-group-gap});
  flex-grow: 1;
  flex-shrink: 0;
  width: $width;
  max-width: $width;
  margin: 0 #{$form-group-gap-half};

  @include media('<mob') {
    display: block;
    width: auto;
    max-width: none;
    margin-bottom: $form-bottom-space; }

  .form-def {
    margin-bottom: 0; }

  .error-feedback,
  .success-feedback {
    float: none;
    position: absolute;
    top: 100%;
    margin-top: 0; } }
