@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* form elements */
label {
  display: inline-block;
  vertical-align: top;
  @include font($font-size, $line-height);
  color: $text-color-light;
  margin-bottom: 0.5em;
  user-select: none;
  -webkit-tap-highlight-color: transparent !important;

  mark {
    background: none;
    color: $error-color; } }

// Input Fields:
// input[type="text"]
// input[type="password"]
// input[type="email"]
// input[type="search"]
// textarea

.form-def {
  display: block;
  padding: $form-input-space-vt $form-input-space-h $form-input-space-vb;
  width: 100%;
  height: $form-input-height;
  background-color: $form-bg-color;
  box-shadow: none;
  border: $form-border-width solid $form-border-color;
  cursor: pointer;

  // Стили для текста
  @include inputs-font;

  // Вертикальные внешние отступы
  @include forms-space;

  // Последний элемент в списке
  &:last-child {
    margin-bottom: 0; } }

textarea {
  $lines: 2;
  $height: round($form-line-height*$lines) + $form-input-space-vt + $form-input-space-vb + round($form-border-width*2);
  min-height: $height;
  height: $height;
  resize: vertical; }

// Placeholder
input, textarea {
  @include placeholder {
    color: $form-placeholder-color;
    transition: $animation-speed opacity; }

  //form placeholder hide on focus
  @include placeholder-focus {
    opacity: 0; } }

// States
.form-def {
  &[disabled] {
    cursor: default;
    color: $form-text-color--disabled;
    background-color: $form-bg-color--disabled;
    border-color: $form-border-color--disabled;

    @include placeholder {
      color: $form-text-color--disabled; } }

  &:focus {
    cursor: text; }

  &:not([disabled]):not(.error):not(.success):not(.filled) {
    //form hover
    &:hover {
      border-color: $form-border-color--focus; }

    //form focus
    &:focus {
      border-color: $form-border-color--focus; } }

  // has value (js)
  //style for label
  @at-root label {
    &.filled {
      @include form-has-value; } }

  &.filled:not([disabled]) {
    @include form-has-value; }

  // on focus (js)
  &.focused {
    @include form-on-focus; }

  //form success
  //style for label
  @at-root label {
    &.success {
      @include form-success; } }

  &.success:not([disabled]) {
    @include form-success;

    //form success placeholder
    @include placeholder {
      color: $form-success-color; } }

  //form error
  @at-root label {
    &.error {
      @include form-error; } }

  &.error:not([disabled]) {
    @include form-error;

    //form error placeholder
    @include placeholder {
      color: $form-error-color; } } }

@import "group";
@import "checkboxes";
@import "select";
@import "select2";
@import "validation";
@import "captcha";

/* form elements end */
